      InstructionPara1:"In a pressure cooker, add the soaked masoor dal, turmeric powder, and enough water to cover the dal. Pressure cook for about 3-4 whistles or until the dal is soft and cooked through.In a separate pan, heat oil or ghee. Add cumin seeds and let them splutter.Add chopped onions and saut until they become translucent.Add ginger-garlic paste and green chilies. Saut for 1-2 minutes until the raw smell disappears.Stir in turmeric powder, coriander powder, and red chili powder. Cook the spices for a minute until they release their aroma.Add chopped tomatoes and cook until they become soft and the oil starts to separate from the masala.Once the dal is cooked, add it to the tempering mixture. Mix well and let it simmer for a few minutes, allowing the flavors to blend.Adjust the consistency of the dal by adding water if needed. Season with salt and garam masala. Mix well and let it simmer for an additional 5-7 minutes.Garnish the Masoor Dal with fresh coriander leaves. Serve hot with steamed rice or Indian bread (roti, naan). Add a squeeze of lemon juice before serving for a burst of freshness",
      IngredientsImage: "https://recipe52.com/wp-content/uploads/2022/05/moong-masoor-dal-3.jpg",
      IngredientsPara:"1 cup masoor dal (red lentils), washed and soaked for 30 minutes.1 large onion, finely chopped.2 tomatoes, chopped.2 green chilies, slit.1 tablespoon ginger-garlic paste.1/2 teaspoon turmeric powder.1 teaspoon cumin seeds.1 teaspoon coriander powder.1/2 teaspoon red chili powder (adjust to taste).1/2 teaspoon garam masala.1 tablespoon cooking oil or ghee.Salt to taste.Fresh coriander leaves for garnish.Lemon wedges for serving",
      TipsImage:"https://i.pinimg.com/originals/56/0a/56/560a56cbd491cc998a01653be9af7a93.jpg",
      TipsPara:"Soaking the masoor dal for at least 30 minutes before cooking can help it cook more evenly and reduce the overall cooking time.Use a pressure cooker to cook the dal. It speeds up the cooking process and ensures that the lentils are tender. Be cautious with the number of whistles, as overcooking can result in mushy dal.Use fresh tomatoes, onions, and green chilies for better flavor. Fresh ingredients contribute significantly to the taste of the dal.The tempering process (cumin seeds, onions, ginger-garlic paste) adds depth and aroma to the dal. Ensure the onions are cooked well and reach a golden brown color for a rich flavor.Adjust the red chili powder and green chilies according to your spice preference. If you prefer it milder, reduce the quantities, and if you like it spicier, feel free to increase them"
      

