      TipsPara:"Allow the batter to ferment for at least 15-20 minutes. Fermentation is essential for the dhokla to rise and become light and spongy.Ensure that the batter is smooth and free of lumps. The consistency should be like pancake batter - neither too thick nor too thin.Use Eno fruit salt or a mixture of baking soda and lemon juice just before steaming. This is what helps the dhokla rise and become fluffy. Mix it gently and uniformly in one direction.Grease the steamer tray or cake tin well to prevent the dhokla from sticking. You can use oil or cooking spray for greasing.Preheat the steamer before pouring the batter. Ensure that the water in the steamer is boiling when you place the tray with the batter"
      
      
