      InstructionPara1:"In a large skillet or saucepan, heat some olive oil over medium heat. Add the finely chopped onion, carrots, and celery. Cook until the vegetables are softened, about 5-7 minutes.Add the minced garlic and cook for an additional minute. Add the ground beef (or pork and beef mix) and brown it, breaking it apart with a spoon as it cooks.Pour in the red wine (if using) and let it simmer for a couple of minutes, scraping up any browned bits from the bottom of the pan.Add the crushed tomatoes and tomato paste to the skillet. Stir well to combine.Pour in the beef or vegetable broth. Add dried oregano, basil, thyme, salt, black pepper, and a pinch of sugar if needed. Stir to combine.Bring the sauce to a simmer, then reduce the heat to low. Let it simmer for at least 30 minutes to allow the flavors to meld. If the sauce gets too thick, you can add a bit more broth.Stir in the whole milk or heavy cream to add richness to the sauce. Simmer for an additional 10-15 minutes.While the sauce is simmering, cook the spaghetti according to the package instructions until al dente, Drain.Spoon the Bolognese sauce over the cooked spaghetti. Serve with grated Parmesan cheese on top and garnish with fresh basil or parsley if desired.Enjoy your delicious Spaghetti Bolognese!",
