      InstructionPara1:"Preheat your oven to 325F (160C). Grease the bottom and sides of a 9-inch (23 cm) springform pan.In a bowl, mix together graham cracker crumbs, sugar, and melted butter until the crumbs are evenly coated. Press the mixture into the bottom of the prepared springform pan to create the crust.Bake the crust in the preheated oven for 10 minutes. Remove from the oven and let it cool while preparing the filling. Keep the oven on.In a large mixing bowl, beat the softened cream cheese until smooth.Add the granulated sugar and vanilla extract to the cream cheese. Beat until well combined.Add the eggs, one at a time, beating well after each addition. Scrape down the sides of the bowl as needed.Mix in the sour cream and heavy cream until the batter is smooth and creamy.Pour the cream cheese filling over the prepared crust in the springform pan.Use a spatula to smooth the top of the cheesecake batter.Bake the cheesecake in the preheated oven for approximately 60-70 minutes or until the edges are set, and the center is slightly jiggly.Allow the cheesecake to cool in the oven with the door ajar for about an hour. Then, refrigerate it for at least 4 hours or overnight to allow it to set.Once the cheesecake is chilled and set, you can add your favorite topping. This could be a fruit compote, chocolate ganache, whipped cream, or anything of your choice.Use a sharp knife to loosen the edges of the cheesecake from the springform pan. Remove the sides of the pan, slice, and serve.Enjoy your creamy and delicious New York-style cheesecake!",
